.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Custom CSS for the app */
.navbar {
  padding: 12px 22px;
  background-color: blueviolet;
}

.list {
  display: flex;
  align-items: center;
  justify-content: center;
}

.list .item {
  list-style-type: none;
  margin-right: 22px;
}

.list .item .link {
  text-decoration: none;
  color: #FFF;
}

.alert {
  position: fixed;
  top: 62px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 40%;
  box-shadow: 4px 4px 8px green;
  text-align: center;
}

/* Theme change toggle Buttom */

.change-theme {
  position: absolute;
  top: 20px;
  right: 20px;
}

@media screen and (max-width: 767px) {
  .alert {
    width: 90%;
  }
}
